import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

@Component({
  selector: 'sof-license-plate',
  styleUrls: ['./license-plate.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class="license-plate license-plate-{{ localCountryCode }}">
      <div
        *ngIf="licensePlateCountryCodes[localCountryCode] as code"
        class="country-code"
      >
        {{ code }}
      </div>
      <span class="value">
        {{ value }}
      </span>
    </div>
  `
})
export class LicensePlateComponent {
  localCountryCode: string;

  /**
   * Country code is specified in ISO Alpha 2 format.
   * ex: BE, NL, DE, etc
   */
  @Input() set countryCode(value: string) {
    this.localCountryCode = value?.toLocaleLowerCase();
  }

  @Input() value: string;

  licensePlateCountryCodes = {
    ['be']: 'B',
    ['de']: 'D',
    ['fr']: 'F',
    ['lu']: 'L',
    ['nl']: 'NL'
  };
}
